  
  /*-----------------------------------*\
    #NEWSLETTER
  \*-----------------------------------*/
  
  .newsletter { color: var(--white); }
  
  .newsletter .container {
    display: grid;
    gap: 50px;
  }
  
  .newsletter-banner { max-width: max-content; }
  
  .newsletter .section-subtitle { color: var(--light-gray); }
  
  .newsletter .section-title {
    color: var(--white);
    margin-block: 20px 35px;
  }
  
  .email-field {
    background-color: var(--majorelle-blue);
    color: var(--white);
    font-size: var(--fs-9);
    min-height: 60px;
    padding-inline: 20px;
    border-radius: var(--radius-8);
    margin-block-end: 20px;
    box-shadow: var(--shadow-5);
    outline: 3px solid transparent;
    outline-offset: 0;
    transition: var(--transition-1);
  }
  
  .email-field:focus { outline-color: var(--white); }
  
  .email-field::placeholder { color: inherit; }

  @media (min-width: 768px) {
    .newsletter-form { position: relative; }
  
    .email-field { margin-block-end: 0; }
  
    .newsletter-form .btn-secondary {
      position: absolute;
      top: 5px;
      right: 5px;
      bottom: 5px;
      height: auto;
      padding-inline: 25px;
    }
  }
    @media (min-width: 992px) {
      .newsletter .container {
        grid-template-columns: 1fr 0.8fr;
        align-items: center;
      }
    
      .newsletter-banner {
        order: 1;
        margin-inline-start: auto;
      }
    
      .email-field {
        min-height: 80px;
        padding-inline: 40px;
      }
    
      .newsletter-form .btn-secondary {
        top: 10px;
        right: 10px;
        bottom: 10px;
      }
    }
